export function formatPrice(price) {
	return price.toLocaleString('fr-FR', {
		style       : 'currency',
		currency    : 'EUR',
		useGrouping : 'true'
	});
}

export function rando(arr) {
	return arr[Math.floor(Math.random() * arr.length)];
}

export function slugify(text) {
	return text
		.toString()
		.toLowerCase()
		.replace(/\s+/g, '-')
		.replace(/[^\w-]+/g, '')
		.replace(/--+/g, '-')
		.replace(/^-+/, '')
		.replace(/-+$/, '');
}

